.wrapper {
    display: flex;
    flex-direction: column;
    width: 100%;
    min-height: 100px;
    background: rgb(255, 255, 255);
    box-shadow: rgb(0 0 0 / 12%) 0px 2px 12px;
    border-radius: 8px;
    max-height: min((100vh - 96px) - 60px, 734px);
    // max-height: min(calc((100vh - 96px) - 60px), 734px);max-height: calc(min((100vh - 96px) - 60px, 734px));
    padding-top: 8px;
}
