.wrapper {
    padding: 20px 0;
    max-width: 692px;
    display: flex;
    align-items: flex-start;
    position: relative;
    margin-left: 70px;
    border-bottom: 1px solid rgba(22, 24, 35, 0.12);
}

.avatar {
    width: 56px;
    height: 56px;
    border-radius: 50%;
    object-fit: cover;

    &:hover {
        cursor: pointer;
    }
}

.content {
    margin-left: 12px;
    width: 624px;
}

.info-content {
}

.author-content {
}

.nick-name {
    font-family: 'IBM Plex Sans', SofiaPro, Arial, Tahoma, PingFangSC,
        sans-serif;
    font-weight: 700;
    font-size: 18px;
    line-height: 25px;
    display: inline-block;
    margin-right: 4px;

    &:hover {
        cursor: pointer;
        text-decoration: underline;
    }
}

.full-name {
    font-family: 'IBM Plex Sans', ProximaNova, Arial, Tahoma, PingFangSC,
        sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    display: inline-block;
    line-height: 28px;
    cursor: pointer;
}

.btn_follow {
    position: absolute;
    top: 28px;
    right: -5px;
}

.text-content {
    width: 500px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    font-size: 1.6rem;
    line-height: 2.2rem;
}

.text-content > a {
    font-weight: 600;
    margin-right: 5px;

    &:hover {
        cursor: pointer;
        text-decoration: underline;
    }
}

.desc-video {
    font-weight: 400;
    margin-right: 5px;
}

.music-content {
    font-weight: 600;
    margin: 5px 0;
    &:hover {
        cursor: pointer;
        text-decoration: underline;
    }
}

.hashtag {
    width: 16px;
    height: 16px;
    margin-right: 5px;
}

.video-container {
    position: relative;
    display: flex;
}

.video_content {
    position: relative;

    .btn_toggle_video {
        left: 16px;
        bottom: 32px;
        display: none;
        position: absolute;
        cursor: pointer;
    }

    .action_right {
        position: absolute;
        bottom: 32px;
        right: 16px;
        display: none;
        flex-direction: column;
        align-items: center;

        .wrapper_volumn {
            display: none;
            position: relative;
            padding: 32px 13px;
            margin-bottom: 10px;
            border-radius: 32px;
            background-color: #16182357;
        }

        &:hover .wrapper_volumn {
            display: block;
            margin-left: -2px;
            animation: show 1s linear;
        }
        @keyframes show {
            from {
                display: none;
            }
            to {
                display: block;
            }
        }
    }

    &:hover .btn_toggle_video {
        display: block;
    }

    &:hover .action_right {
        display: flex;
    }
}

.video {
    // width: 100%;
    height: 514px;
    border-radius: 8px;
    object-fit: cover;

    &:hover {
        cursor: pointer;
    }
}

.btn_mute,
.btn_volumn {
    cursor: pointer;
}

.btn_toggle_volumn {
    width: 47px;
    height: 2px;
    right: -10px;
    position: absolute;
    border-radius: 4px;
    -webkit-appearance: none;
    background-color: #fff;
    transform: rotate(-90deg);
}

.actions {
    display: flex;
    flex-direction: column;
    margin: 264px 0 0 20px;

    p {
        color: rgba(22, 24, 35, 1);
        font-size: 12px;
        line-height: 17px;
        text-align: center;
    }

    &:hover {
        cursor: pointer;
    }
}

.action_btn {
    padding: 12px;
    background-color: #1618230f;
    border-radius: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 8px 0;
}
