.wrapper {
    min-width: 100px;
    padding: 6px 18px;
    border-radius: 4px;
    font-size: 1.6rem;
    font-weight: 700;
    line-height: 22px;
    border: 1px solid transparent;
    background-color: var(--white);
    user-select: none;
    cursor: pointer;

    // tại sao lại ko ăn scss ( chữa chãy = cách scss trong file global)
    & .separate {
        border-top: 1px solid rgba(22, 24, 35, 0.12);
    }

    + .wrapper {
        margin-left: 12px;
    }

    & .disabled {
        pointer-events: none;
        opacity: 0.5;
    }
}

// size btn
.small {
    min-width: 88px;
    padding: 3px 18px;
}

.large {
    min-width: 140px;
    padding: 13px 18px;
}

//type btn
.primary {
    background-color: var(--primary);
    color: var(--white);
    &:hover {
        background: linear-gradient(
                0deg,
                rgba(0, 0, 0, 0.06),
                rgba(0, 0, 0, 0.06)
            ),
            #fe2c55;
    }
}

.outline {
    color: var(--primary);
    border-color: currentColor;

    &:hover {
        background-color: rgba(254, 44, 85, 0.06);
    }
}

.outline_text {
    border-radius: 2px;
    border-color: rgba(22, 24, 35, 0.12);
    &:hover {
        background-color: rgba(22, 24, 35, 0.03);
    }
}

.rounded {
    border-radius: 999px;
    border-color: rgba(22, 24, 35, 0.12);
    box-shadow: 0 2px 8px rgb(0 0 0 / 6%);

    &:hover {
        border-color: rgba(22, 24, 35, 0.2);
        background-color: rgba(255, 255, 255, 1);
    }
}

.icon + .title,
.title + .icon {
    margin-left: 2px;
}
.icon {
    display: inline-block;
    width: 24px;
}
.icon-right {
    font-size: 2.6rem;
    margin-left: 46px;
    transform: translateY(-10%);
}
